.time {
  bottom: 4vh;
  display: flex;
  position: fixed;
}

.displayChat {
  display: flex;
  width: 100%;
  height: 100%;
  /* animation: drop 1s ease; */
  -webkit-animation-name: stretch;
          animation-name: stretch;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  /* animation-direction: alternate; */
  -webkit-animation-iteration-count: drop;
          animation-iteration-count: drop;
  -webkit-animation-fill-mode: none;
          animation-fill-mode: none;
  background-color: #edf0f5;
  /* animation-play-state: running; */
}

@-webkit-keyframes stretch {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    /* background-color: red; */
    border-radius: 100%;
  }
  50% {
    /* background-color: orange; */
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    /* background-color: yellow; */
  }
}

@keyframes stretch {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    /* background-color: red; */
    border-radius: 100%;
  }
  50% {
    /* background-color: orange; */
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    /* background-color: yellow; */
  }
}

#video-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  grid-auto-rows: 300px;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.head {
  padding-left: 1rem;
  padding-right: 1rem;
  border-radius: 5px;
  border: none;
  font-size: 16px;
}

.head:hover {
  background-color: #d1e6e7;
  color: #00a389;
  font-size: 18px;
  height: 28px;
}

.chatbox {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.chat-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  /* background-color: #edf0f5; */
  height: 86vh;
  overflow-y: auto;
}

.chatRoomMain {
  width: 100%;
}

#form {
  background: none;
  padding: 0.25rem;
  display: flex;
  box-sizing: border-box;
  box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  width: 100%;
}

#message {
  border: none;
  font-size: 14px;
  padding: 0 1rem;
  flex-grow: 1;
  border-radius: 2rem;
  margin: 0.25rem;
  width: 100%;
}

#message:focus {
  outline: none;
}

#form > button {
  display: flex;
  background: #00a389;
  border: none;
  cursor: pointer;
  padding: 0 1rem;
  margin: 0.25rem;
  border-radius: 3px;
  outline: none;
  color: #fff;
  align-items: center;
}

.msg {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

#messages {
  list-style-type: none;
  margin: 0;
  padding-bottom: 5rem;
  width: 100%;
}

.my-message {
  margin: 10px;
}

.msg .my-message {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.received-message {
  margin: 10px;
}

.my-message .msgBody {
  display: flex;
  flex-direction: column;
  background-color: #d0d3e3;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 20px;
  padding: 0.5rem 1rem;
  border-bottom-right-radius: 0px;
}

.received-message .msgBody {
  display: flex;
  flex-direction: column;
  background-color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 20px;
  padding: 0.5rem 1rem;
  border-bottom-left-radius: 0px;
}

.displayEmoji {
  display: flex;
  padding: 0.25rem;
  position: fixed;
  bottom: 4rem;
  right: 8rem;
  /* left: 10px; */
  /* box-sizing: border-box; */
  /* box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%); */
  /* backdrop-filter: blur(10px); */
}

.notDisplayEmoji {
  display: none;
}

.emoji-picker-react {
  background: none !important;
  box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%) !important;
  -webkit-backdrop-filter: blur(10px) !important;
          backdrop-filter: blur(10px) !important;
}

.emoji-search {
  background: none !important;
}

.emoji-group:before {
  background-color: #edf0f5 !important;
}

.emoji-scroll-wrapper {
  background: none !important;
}

.camera {
  display: flex;
}

a {
  color: #00a389;
}

a:hover {
  color: #00705e;
}

.group_chat_bar {
  display: flex;
  justify-content: space-between;
  padding: 1rem 1rem;
  box-sizing: border-box;
  width: 100%;
  background: none;
  border-bottom: 1px solid #d0d3e3;
  height: 3rem;
}

@supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
  .group_chat_bar {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
  }
}

